input:focus,
button:focus {
  outline: none;
}
.app {
  display: flex;
  justify-content: center;
  align-items: center;
}
.registerContainer {
  width: 400px;
  padding: 80px;
  box-shadow: 0px 0px 20px 0px #32322c;
  background-color: #2a2e45;
}
.headingR {
  text-align: center;
  color: white;
  font-weight: 600;
}
.registerForm {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.nameInput {
  height: 50px;
  padding-left: 10px;
  margin-bottom: 30px;
  align-self: stretch;
  color: white;
  background-color: transparent;
  border: none;
  border-bottom: solid 1px rgba(255, 255, 255, 0.7);
}
.checkInput {
  width: 15px;
  vertical-align: top;
  margin-right: 10px;
  margin-left: -20px;
  background-color: #59a96a;
}
.checkbox {
  margin-left: 20px;
  vertical-align: bottom;
  color: white;
  font-weight: 600;
}
.btn {
  height: 60px;
  width: 100%;
  padding: 15px 32px;
  margin-top: 40px;
  display: inline-block;
  text-align: center;
  text-decoration: none;
  font-weight: 600;
  font-size: 22px;
  border: none;
  border-radius: 40px;
  color: white;
  background-color: #db5461;
  box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.3);
  cursor: pointer;
}
.btn:active {
  transform: scale(0.9);
  transition-duration: 100ms;
}

.disabled {
  background-color: rgba(255, 255, 255, 0.2);
  color: rgba(255, 255, 255, 0.7);
  height: 60px;
  width: 100%;
  margin-top: 40px;
  display: inline-block;
  text-align: center;
  text-decoration: none;
  font-weight: 600;
  font-size: 22px;
  border: none;
  border-radius: 40px;
}
.disable:active {
  transform: none;
}
